import React from 'react'
import Link from 'gatsby-link'
//import Punchline from '../components/Punchline'
import Card from '../components/Card'
import LowcodeLogo from '../images/low-code-icon.png';
import DevelLogo from '../images/custom-development-icon.png';
import MobileLogo from '../images/mobile-icon.png';
import BotsLogo from '../images/bots-icon.png';
import IotLogo from '../images/iot-icon.png';
import EinsteinLogo from '../images/einstein-icon.png';
import Punchline from '../components/Punchline'

const CardPanel = ({title, details, logo}) => 
(
	<div className="content-section col-sm-6 col-md-4">
	    <div className="row">
	    	<div className="col-sm-4 col-lg-3 section-icon">
	        	<img src={logo} alt="logo" />
	        	<h3 className="section-title">{title}</h3>
	        </div>
	        <Card title={title} details={details}/>
	    </div>
    </div>
);

const IndexPage = () => 
(
	<div>
	<Punchline tag="Integrate force.com with open source enterprise friendly library to demonstrates the 
		unique value business proposition for Customer Engagement."/>
	<div className="container main-content-sections">
		<div className="row">
			<CardPanel logo={LowcodeLogo} title="Global Airlines Carrier" details="CRM integration with deal modelling software through Informatica cloud, 
				Partner data migration/ compliance, Multi-lingual LEX (lightning compliant) compliant community portal implementation."/>
			<CardPanel logo={EinsteinLogo} title="UK Insurance Company" details="Integration Architect for Salesforce Service Cloud implementation
			 	connected with Mule ESB/ Avaya Telephony/ Social Media/ Community/ Informatica MDM."/>
			<CardPanel logo={MobileLogo} title="ANZ Healthcare" details="Technical Architect for Mobility solution integration with Inverse data 
				sharing model and Offline data synchronization with multi-org Force.com instances."/>
		</div>
		<br/>
		<div className="row">
			<CardPanel logo={IotLogo} title="Centre Of Excellence" details="Solution design & Salesforce based Web/ Rest/ Bulk/ Metadata/ oAuth/ SSO/ API
				 services integration with sales automation. Solution extension for Force.com Dataloader & Ant metadata migration for deployment / ETL process automation."/>
			<CardPanel logo={DevelLogo} title="Prepaid Telecom Billings" details="Prepaid billing systems framework with Mule ESB, 
				Business rules & processes integration. Automation  and continuous integration with Maven, Sonar, Jenkins & Webdriver"/>
			<CardPanel logo={BotsLogo} title="Online Distribution Management" details="Development of Real time web data mining for hospitality product 
				development.  Solution deployment in Unix servers with load balancer approach."/>
		</div>
	</div>
	</div>
);

export default IndexPage



