import React from 'react';
import Link from 'gatsby-link'

const Card = ({ title, details, blogparam }) => (
  <div className="col-sm-8 col-lg-9">
    <h3 className="section-title">{title}</h3>
    <p className="section-content">{details}</p>
    <ul className="section-posts fa-ul">
    <li className="section-post"><a href="/blog" data-toggle="tooltip" data-placement="auto right" title="Thanks for patience, will be connected with blog shortly."><i className="fa-li fa fa-angle-right"></i>Know More</a></li>
    </ul>
  </div>
)

export default Card



