import React from 'react';
import Link from 'gatsby-link'

const Punchline = ({tag}) =>
(
    <div className="container main-content">
        <div className="row">
            <div className="col-sm-12">
                <p className="heading-content">{tag}</p>
            </div>
        </div>
    </div>
);

export default Punchline


